<template>
  <div class="tile is-parent">
    <div style="display: flex; flex-direction: column">
      <article ref="ml" id="messageList" class="tile is-child notification is-success"
               style="height: 600px; width: 300px; overflow-y: scroll; word-wrap: break-word">
        <div
            v-for="msg in messages"
            :key="msg.id"
            class="content has-text-left"
        >
          <p>{{ msg.username }}: {{ msg.message }}</p>
        </div>
      </article>
      <div>
        <input class="input is-fullwidth" v-model="message" type="text" placeholder="Text input"
               v-on:keyup="sendMessage">
      </div>
    </div>
  </div>
</template>

<script>
import RoomService from "@/services/RoomService";

export default {
  name: "ChatBox",
  data() {
    return {
      messages: [],
      message: "",
    }
  },
  created() {
    this.getRoomMessages();
  },
  mounted() {

  },
  methods: {
    async getRoomMessages() {
      // Use the RoomService to call the getRoomSingle(roomId) method
      await RoomService.getRoomMessages(this.$route.params.id)
          .then(
              (messages => {
                this.$set(this, "messages", messages);
              }).bind(this)
          );
      this.scrollMessageListDown();
    },
    async sendMessage(e) {
      if (e.keyCode === 13 && this.message !== "") {
        e.preventDefault();
        let req = await RoomService.sendMessage(this.message);
        // let sendMessage = null;
        if (req.err === 0) {
          // sendMessage = req.data;
        } else {
          this.redirect2Lobby();
          return;
        }
        this.message = "";
      }
    },
    scrollMessageListDown() {
      let content = this.$refs.ml;
      if (content) {
        content.scrollTop = content.scrollHeight;
      }
    },
    redirect2Lobby() {
      if (this.$route.path !== "/") {
        this.$router.push({
          path: "/",
          name: 'lobby',
          params: {},
        });
      }
    },
  },
  sockets: {
    message(data) {
      console.log('message:' + data);
      let dataObj = JSON.parse(data);
      this.messages.push(dataObj);
    },
  },
  updated: function () {
    this.$nextTick(function () {
      this.scrollMessageListDown();
    })
  },
}
</script>

