<template>
  <div>
    <Nav ref="nav"/>
    <div v-if="showAnswer">
      <div class="modal is-active">
        <div class="modal-background"></div>
        <div class="modal-card" style="max-width: 300px">
          <header class="modal-card-head">
            <p class="modal-card-title">Answer</p>
            <button class="delete" aria-label="close" @click.left="showAnswer=false"></button>
          </header>
          <section class="modal-card-body">
            <h1> {{ answer }} </h1>
          </section>
        </div>
      </div>
    </div>
    <div id="navbar" class="navbar-menu" style="margin-left: 100px; margin-right: 100px;">
      <div class="navbar-start">
        <div class="buttons">
          <button class="button is-primary" @click.left="clearHandler($event)" :disabled="!myTurn">
            <strong>Clear</strong>
          </button>
          <button class="button is-light" @click.left="revokeHandler($event)" :disabled="!myTurn">
            Revoke
          </button>
          <a class="button is-light" @click.left="testHandler($event)">
            TEST
          </a>
        </div>
      </div>
      <div class="navbar-end" v-if="!gameStart">
        <div class="navbar-item" v-if="checkIfIsRoomOwner()">
          <button class="button is-primary" @click.left="play($event)">
            <strong>Play</strong>
          </button>
        </div>
        <div class="navbar-item" v-else>
          <button class="button is-primary" @click.left="ready($event)">
            <strong v-if="isReady">Unready</strong>
            <strong v-else>Ready</strong>
          </button>
        </div>
      </div>
    </div>
    <div v-show="word !== ''" class="level-item">
      <p class="subtitle is-5">
        <strong>Word:</strong> {{ word }}
      </p>
    </div>
    <div class="tile is-ancestor" style="margin-left: 100px; margin-right: 100px;">
      <div class="tile is-vertical is-9">
        <div class="tile is-parent">
          <div class="tile is-child notification is-warning" style="text-align: center">
            <PaperCanvas :canvas-id="'canvas-one'" ref="pc"/>
            <div class="is-overlay" v-if="!myTurn">
            </div>
          </div>
        </div>
        <SeatList v-if="enterSuccess" :roomOwnerId="roomOwnerId" ref="sl" @unReady="unReady" @IamOwner="IamOwner"/>
      </div>
      <ChatBox/>
    </div>
    <div v-if="enterPassword">
      <div class="modal is-active">
        <div class="modal-background"></div>
        <div class="modal-card" style="max-width: 300px">
          <header class="modal-card-head">
            <p class="modal-card-title">Room Password</p>
            <button class="delete" aria-label="close" @click.left="enterPassword=false"></button>
          </header>
          <section class="modal-card-body">
            <input class="input is-info" type="password" v-model="inputRoomPassword" placeholder="Password Here">
          </section>
          <footer class="modal-card-foot" style="justify-content: flex-end;">
            <button class="button is-primary" @click.left="enterWithPassword($event)">
              Enter
            </button>
          </footer>
        </div>
      </div>
    </div>
    <div v-if="showScores">
      <div class="modal is-active">
        <div class="modal-background"></div>
        <div class="modal-card" style="max-width: 300px">
          <header class="modal-card-head">
            <p class="modal-card-title">Scores</p>
            <button class="delete" aria-label="close" @click.left="showScores=false"></button>
          </header>
          <section class="modal-card-body">
            <div
                v-for="score in scores"
                :key="score.userId"
                class="content has-text-left"
            >
              <p>{{ score.username }}: {{ score.score }}</p>
            </div>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RoomService from "@/services/RoomService";
import SeatList from "@/components/SeatList";
import ChatBox from "@/components/ChatBox";
import PaperCanvas from "@/components/PaperCanvas";
import Nav from "@/components/partials/Nav";

export default {
  name: "RoomSingle",
  components: {ChatBox, SeatList, PaperCanvas, Nav},
  data() {
    // Initialize the room object
    return {
      roomInfo: null, // 1
      gameState: null, // 1
      roomOwnerId: -1, // 1
      isRoomOwner: false, // 1
      myTurn: false, // 1
      isReady: false, // 1
      enterPassword: false,
      inputRoomPassword: "",
      enterSuccess: false, // 1
      gameStart: false, // 1
      word: "", // 1
      showAnswer: false,
      answer: "",
      showScores: false,
      scores: {},
    }
  },
  async mounted() {

  },
  async created() {
    this.$socket.disconnect();
    // Check if already in the room
    let inRoom = await RoomService.isReady();
    console.log(inRoom);
    if (inRoom.err !== 0) {
      // Pre check if room available
      let roomInfo = await RoomService.fetchRoomBasicInfo(this.$route.params.id);
      let roomInfoData = null;
      if (roomInfo.err === 0) {
        roomInfoData = roomInfo.data;
        if (Number(roomInfoData.start) === 1) {
          alert("Game has started.");
          return;
        }
      } else {
        alert(roomInfo.errMsg);
        this.redirect2Lobby();
        return;
      }
      this.roomInfo = roomInfoData;
      // Check if the user is the room owner.
      if (Number((roomInfoData.roomOwner.split(":"))[0]) !== Number(this.$cookies.get("userId"))) {
        // Check if need password.
        let needPassword = await RoomService.needPassword(roomInfoData.roomId);
        if (needPassword.err === 0) {
          needPassword = needPassword.data;
          if (needPassword) {
            this.enterPassword = true;
            return;
          }
        } else {
          alert(needPassword.errMsg);
          return;
        }
        // Enter room request.
        let enterRoom = await RoomService.enterRoom(roomInfoData.roomId, roomInfoData.roomName, this.inputRoomPassword);
        if (enterRoom.err !== 0) {
          alert(enterRoom.errMsg);
          if (enterRoom.err === -1 || enterRoom.err === 2 || enterRoom.err === 3 || enterRoom.err === 4) {
            this.redirect2Lobby();
          }
          return;
        }
      }
    }
    // Enter success.
    this.enterSuccess = true;
    await this.fetchAllNecessaryInfo();
    // Open SocketIO
    // this.$socket.disconnect();
    this.$socket.connect();
  },
  sockets: {
    disconnect() {

    },
    connect_error() {
      alert("Socket open failure.");
      this.redirect2Lobby();
    },
    async connect() {
      console.log('socket connected!!!!!!!!!');
      // Socket join room.
      let bindRoom = await RoomService.bindRoom();
      if (bindRoom.err !== 0) {
        alert(bindRoom.errMsg);
        this.redirect2Lobby();
      }
    },
    async drawWord(data) {
      if (this.myTurn) {
        await this.$refs.pc.saveCanvasToPng(this.word);
      }
      this.word = JSON.parse(data).word;
      this.$refs.pc.reset();
      this.myTurn = this.word !== "";
    },
    answer(data) {
      this.answer = JSON.parse(data).word;
      this.showAnswer = true;
    },
    async gameStart() {
      this.gameStart = true;
      this.$refs.sl.setGameStart(true);
    },
    async gameOver(data) {
      if (this.myTurn) {
        await this.$refs.pc.saveCanvasToPng(this.word);
      }
      this.scores = JSON.parse(data).scores;
      console.log(this.scores);
      this.showScores = true;
      this.isReady = false;
      this.gameStart = false;
      this.$refs.pc.reset();
      this.myTurn = false;
      this.$refs.sl.setGameStart(false);
      this.isRoomOwner = this.roomOwnerId === Number(this.$cookies.get("userId"));
      this.word = "";
      await this.$refs.sl.refreshSeat();
    },
    refreshSeat() {
      this.$refs.sl.refreshSeat();
    },
  },
  methods: {
    async enterWithPassword() {
      let enterRoom = await RoomService.enterRoom(this.roomInfo.roomId, this.roomInfo.roomName, this.inputRoomPassword);
      if (enterRoom.err === 0) {
        this.enterSuccess = true;
        this.enterPassword = false;
        await this.fetchAllNecessaryInfo();
      } else {
        alert(enterRoom.errMsg);
        if (enterRoom.err === -1 || enterRoom.err === 2 || enterRoom.err === 3 || enterRoom.err === 4) {
          this.redirect2Lobby();
        }
      }
    },
    clearHandler(e) {
      e.preventDefault();
      this.$refs.pc.clickClear();
    },
    revokeHandler(e) {
      e.preventDefault();
      this.$refs.pc.clickRevoke();
    },
    testHandler(e) {
      this.myTurn = !this.myTurn;
      this.$refs.pc.saveCanvasToPng((Date.now()).toString());
      e.preventDefault();
    },
    async ready(e) {
      let ready = await RoomService.ready();
      if (ready.err === 0) {
        this.isReady = ready.data === 1;
        this.$refs.sl.clickReady(this.isReady);
      } else {
        alert(ready.errMsg);
        if (ready.err !== 2) {
          this.redirect2Lobby();
          return;
        }
      }
      if (e) {
        e.preventDefault();
      }
    },
    async play(e) {
      let play = await RoomService.play();
      if (play.err !== 0) {
        alert(play.errMsg);
        if (play.err !== 5 && play.err !== 4 && play.err !== 3) {
          this.redirect2Lobby();
          return;
        }
      }
      if (e) {
        e.preventDefault();
      }
    },
    unReady() {
      this.isReady = false;
    },
    async IamOwner() {
      await this.fetchAllNecessaryInfo();
    },
    redirect2Lobby() {
      if (this.$route.path !== "/") {
        this.$router.push({
          path: "/",
          name: 'lobby',
          params: {},
        });
      }
    },
    async fetchAllNecessaryInfo() {
      let roomInfo = await RoomService.fetchRoomBasicInfo(this.$route.params.id);
      let roomInfoData = null;
      if (roomInfo.err === 0) {
        roomInfoData = roomInfo.data;
      } else {
        alert(roomInfo.errMsg);
        this.redirect2Lobby();
        return;
      }
      this.roomInfo = roomInfoData;
      let getIsReady = await RoomService.isReady();
      if (getIsReady.err === 0) {
        this.isReady = getIsReady.data;
      } else {
        this.redirect2Lobby();
        return;
      }
      let getGameState = await RoomService.getGameState(this.$route.params.id);
      if (getGameState.err !== 0) {
        alert(getGameState.errMsg);
        this.redirect2Lobby();
        return;
      }
      let gameState = getGameState.data;
      this.gameState = gameState;
      if (gameState.start === 0) {
        this.word = "";
        this.gameStart = false;
      } else {
        this.gameStart = true;
        this.word = gameState.drawWord;
      }
      let roomOwner = this.roomInfo.roomOwner.split(":");
      this.roomOwnerId = Number(roomOwner[0]);
      if (this.roomOwnerId === Number(this.$cookies.get("userId"))) {
        this.isRoomOwner = true;
      }
      if (this.word !== "") {
        this.myTurn = true;
      }
    },
    checkIfIsRoomOwner() {
      return this.isRoomOwner;
    },
  },
}
</script>

