<template>
  <div class="tile is-parent">
    <article class="tile is-child notification is-warning">
      <div class="columns is-multiline">
        <div
            v-for="(seat, key) in seats"
            :key="seat.id"
            class="column is-one-quarter"
        >
          <template v-if="seat.userId === -1">
            <button class="button is-fullwidth" @click.left="leftClickHandler($event, key)"
                    @contextmenu="rightClickHandler($event, seat)">{{
                seat.username
              }}
            </button>
          </template>
          <template v-else>
            <button v-if="seat.userId === ownerId" class="button is-primary is-fullwidth"
                    @contextmenu="rightClickHandler($event, seat)">{{
                seat.username
              }}
            </button>
            <button v-else-if="seat.userReady" class="button is-link is-fullwidth"
                    @contextmenu="rightClickHandler($event, seat)">{{
                seat.username
              }}
            </button>
            <button v-else class="button is-info is-fullwidth" @contextmenu="rightClickHandler($event, seat)">{{
                seat.username
              }}
            </button>
          </template>
        </div>
      </div>
    </article>
  </div>
</template>

<script>
import RoomService from "@/services/RoomService";

export default {
  name: "SeatList",
  props: ['roomOwnerId'],
  data() {
    // Initialize the room object
    return {
      seats: [],
      ownerId: -1,
      ownerName: "",
      isStart: false,
    }
  },
  sockets: {

  },
  components: {},
  async created() {
    await this.getSeats();
    await this.getRoomOwner();
  },
  methods: {
    async getSeats() {
      // Use the RoomService to call the getRoomSingle(roomId) method
      await RoomService.getSeats(this.$route.params.id)
          .then(
              (async seats => {
                if (seats.err !== 0) {
                  alert(seats.errMsg);
                  if (this.$route.path !== "/") {
                    await this.$router.push({
                      path: "/",
                      name: 'lobby',
                      params: {},
                    });
                    return;
                  }
                }
                this.$set(this, "seats", seats.data);
              }).bind(this)
          );
    },
    async getRoomOwner() {
      // Use the RoomService to call the getRoomSingle(roomId) method
      await RoomService.getRoomOwner(this.$route.params.id)
          .then(
              (async owner => {
                let ownerInfo = {};
                if (owner.err === 0) {
                  ownerInfo = owner.data;
                } else {
                  alert(owner.errMsg);
                  if (this.$route.path !== "/") {
                    await this.$router.push({
                      path: "/",
                      name: 'lobby',
                      params: {},
                    });
                    return;
                  }
                }
                this.$set(this, "ownerId", Number(ownerInfo.ownerId));
                this.$set(this, "ownerName", Number(ownerInfo.ownerName));
              }).bind(this)
          );
    },
    rightClickHandler(e, item) {
      let userId = 99; // TODO: get current user id
      if (item.userId !== -1 && userId === this.roomOwnerId) {
        let success = true;
        //TODO: send kick request
        if (success) {
          for (let i = 0; i < this.seats.length; i++) {
            if (this.seats[i].userId === item.userId) {
              this.seats[i].userId = -1;
            }
          }
        }
      }
      e.preventDefault();
    },
    async leftClickHandler(e, index) {
      if (this.isStart) {
        return;
      }
      let changeSeat = await RoomService.changeSeat(index);
      if (changeSeat.err === 0) {
        this.$emit("unReady");
        await this.getSeats();
      } else {
        alert(changeSeat.errMsg);
        if (changeSeat.err !== 2 && changeSeat.err !== 3) {
          await this.$router.push({
            path: "/",
            name: 'lobby',
            params: {},
          });
        } else {
          await this.getSeats();
        }
      }
      e.preventDefault();
    },
    clickReady(isReady) {
      let tempSeats = this.seats;
      for (let i = 0; i < this.seats.length; i++) {
        let seat = tempSeats[i];
        if (Number(this.$cookies.get("userId")) === seat.userId) {
          this.seats[i].userReady = isReady;
          break;
        }
      }
    },
    async refreshSeat() {
      await this.getSeats();
      await this.getRoomOwner();
      if(this.ownerId === Number(this.$cookies.get("userId"))) {
        this.$emit("IamOwner");
      }
    },
    setGameStart(start) {
      this.isStart = start;
    }
  }
}
</script>

