<template>
  <div>
    <canvas ref="cs" :id="canvasId" class="canvas-style" v-on:mousedown="mouseDown"
    />
  </div>
</template>

<script>
// https://noahkreiger.medium.com/vuejs-paperjs-a-canvas-story-c412dc161b1f
import RoomService from "@/services/RoomService";

const paper = require('paper');
export default {
  name: "Canvas",
  props: ['canvasId'],
  data: () => ({
    path: null,
    scope: null,
    paths: []
  }),
  methods: {
    clickClear() {
      this.reset();
      this.$socket.emit("clear", JSON.stringify({
        "roomId": this.$route.params.id,
      }));
    },
    clickRevoke() {
      this.revoke();
      this.$socket.emit("revoke", JSON.stringify({
        "roomId": this.$route.params.id,
      }));
    },
    reset() {
      this.scope.project.activeLayer.removeChildren();
      this.paths = [];
    },
    revoke() {
      let temp = this.paths.pop();
      if (temp) {
        temp.remove();
      }
    },
    async sendPicture() {
    },
    pathCreate(scope) {
      scope.activate();
      return new paper.Path({
        strokeColor: "#000000",
        strokeJoin: 'round',
        strokeWidth: 1.5
      })
    },
    createTool(scope) {
      scope.activate();
      return new paper.Tool();
    },
    mouseDown() {
      // create drawing tool
      this.tool = this.createTool(this.scope);
      this.tool.onMouseDown = (event) => {
        // init path
        this.path = this.pathCreate(this.scope);
        // add point to path
        this.path.add(event.point);
      };
      this.tool.onMouseDrag = (event) => {
        this.path.add(event);
      };
      this.tool.onMouseUp = (event) => {
        // line completed
        this.path.add(event.point);
        this.paths.push(this.path);
        this.$socket.emit("sendPath", JSON.stringify({
          "roomId": this.$route.params.id,
          "path": JSON.stringify(this.path),
        }));
      }
    },
    async saveCanvasToPng(word) {
      let canvas = this.$refs.cs;
      let dataURL = canvas.toDataURL("image/png");
      await RoomService.sendImage(dataURL, word);
    }
  },
  mounted() {
    this.scope = new paper.PaperScope();
    this.scope.setup(this.canvasId);
  }
  ,
  sockets: {
    async receivePath(data) {
      this.path = this.pathCreate(this.scope);
      let newPath = JSON.parse(data);
      let segments = newPath[1].segments;
      for (let i = 0; i < segments.length; i++) {
        this.path.add(new paper.Point(segments[i][0], segments[i][1]));
      }
      this.paths.push(this.path);
    }
    ,
    clear() {
      this.reset();
    }
    ,
    revoke() {
      this.revoke();
    }
  }
  ,

}
</script>

<style scoped>
.canvas-style {
  cursor: crosshair;
  width: 100% !important;
  height: 400px !important;
  border: 5px solid black;
  border-radius: 10px;
  display: block;
  margin: auto;
  box-shadow: 0 10px 8px -8px black;
  background-color: white;
}
</style>
